
import React, {  useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from "react-router-dom";
//import './Page3.css';//each page css
import Policycss from './Policy.module.css';
import * as DOMPurify from 'dompurify';
import { List, Icon } from 'semantic-ui-react';


const Policy = () =>{

    const [Open, setOpen] = useState("0");
    const { t, i18n } = useTranslation();
    const [LanguageSelected, setLanguageSelected] = useState(i18n.language);
    const navigate = useNavigate();

    const sanitized = DOMPurify.sanitize(t('Policy_body'));
    const changeLanguage = (lng) => {
        i18n.changeLanguage(lng);
        document.title = t('Page_Title');
    };
    useEffect(() => {
        document.title = t('Page_Title');
    }, []);



    return (
        <>
            <div style={{ width: Open }} className={Policycss.NavigateBar}>
                <List divided relaxed size='massive'>
                    <List.Item><img src={require('./image/logo.png')} alt="HAD LOGO"></img></List.Item>
                    <List.Item onClick={() => setOpen("0")}>Close</List.Item>
                    <List.Item onClick={() => navigate('/')}> {t('Home')} </List.Item>
                    <List.Item onClick={() => navigate('/Search', { state: { search: "None" } })}>{t('Search')}</List.Item>
                    <List.Item onClick={() => navigate('/result', { state: { search: "Full" } })}>{t('ListAll')}</List.Item>
                    <List.Item onClick={() => navigate('/help')}>{t('Help')}</List.Item>
                    <List.Item><img src={require('./image/zhcn.png')} onClick={() => { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") }} alt="zh-cn"></img></List.Item>
                    <List.Item><img src={require('./image/zhtw.png')} onClick={() => { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") }} alt="zh-tw"></img></List.Item>
                    <List.Item><img src={require('./image/enus.png')} onClick={() => { changeLanguage("en-US"); setLanguageSelected("en-US") }} alt="English"></img></List.Item>
                </List>
            </div>
            <div className={Policycss.container} >
                <div className={Policycss.responsive}><Icon size='huge' name='bars' className={Policycss.responsiveLogo} onClick={() => setOpen("60%")}> </Icon></div>
                <div className={Policycss.title}>
                    <div>
                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") } }} src={require('./image/zhcn.png')} className={LanguageSelected === "zh-CN" ? Policycss.i18nSelected : Policycss.i18n} onClick={() => { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") }} alt="zh-cn"></img>
                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") } }} src={require('./image/zhtw.png')} className={LanguageSelected === "zh-TW" ? Policycss.i18nSelected : Policycss.i18n} onClick={() => { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") }} alt="zh-tw"></img>
                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("en-US"); setLanguageSelected("en-US") } }} src={require('./image/enus.png')} className={LanguageSelected === "en-US" ? Policycss.i18nSelected : Policycss.i18n} onClick={() => { changeLanguage("en-US"); setLanguageSelected("en-US") }} alt="English"></img>
                        <span tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/text') } }} className={Policycss.textsize} onClick={() => navigate('/text')}> {t('Text')}</span>
                    </div>
                </div>
                <div className={Policycss.logo} >
                    <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} src={require('./image/logo.png')} className={Policycss.HADLOGO} onClick={() => navigate('/')} alt="HAD LOGO"></img>
                    <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} src={require('./image/title_' + i18n.language + '.png')} className={Policycss.HADLOGO} onClick={() => navigate('/')} alt="HAD LOGO"></img>
                </div>
                <div className={Policycss.menu}>
                    <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} onClick={() => navigate('/')} aria-label="Home">{t('Home')}</label>
                    <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/Search', { state: { search: "None" } }) } }} onClick={() => navigate('/Search', { state: { search: "None" } })} aria-label="Search">{t('Search')}</label>
                    <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/result', { state: { search: "Full" } }) } }} onClick={() => navigate('/result', { state: { search: "Full" } })} aria-label="List All building info">{t('ListAll')}</label>
                    <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/help') } }} onClick={() => navigate('/help')} aria-label="Help">{t('Help')}</label>
                </div>
                <div className={Policycss.main}>
                        <div className={Policycss.mainframe}>
                        <div className={Policycss.mainframeTitle} aria-label="Policy title">{t('Policy_title')}</div>
                        <div dangerouslySetInnerHTML={{ __html: sanitized }} className={Policycss.mainframeContent} aria-label="Policy body" />
                    </div>
                    <button className={Policycss.backpage} onKeyDown={(event) => { if (event.key === "Enter") { navigate(-1) } }} onClick={() => navigate(-1)} aria-label="Go back">{t('Back')}</button>
                </div>
                <div className={Policycss.footer}>
                    <div className={Policycss.footerLeftLabel} tabindex="0" ><img className={Policycss.WCAG} src={require('./image/test.png')} alt='WCAG Logo'></img></div>
                    <div className={Policycss.footerLeftLabel} tabindex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/notice') } }} onClick={() => navigate('/notice')} aria-label="Notice">{t('Notice')}</div>
                    <div className={Policycss.footerLeftLabel} tabindex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/policy') } }} onClick={() => navigate('/policy')} aria-label="Policy">{t('Policy')}</div>
                    <div className={Policycss.footerRightLabel} tabindex="0" aria-label="Copyright">{t('Copyright')}</div>
                </div>
            </div>
        </>
    );
}

export default Policy;