import { useLocation } from 'react-router';
import { useNavigate } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { useEffect, useState } from 'react';
import Infocss from './Result_info.module.css';
import { List, Icon, Grid, Menu, Sidebar } from 'semantic-ui-react';

const Info = () => {
    const [responseData, setResponseData] = useState(null);
    const params = useLocation();
    const { t, i18n } = useTranslation();
    const [LanguageSelected, setLanguageSelected] = useState(i18n.language);
    const [Open, setOpen] = useState(false);
    const navigate = useNavigate();
    const [SearchKey, setSearchKey] = useState(null);

    const changeLanguage = (lng) => {
        i18n.changeLanguage(lng);
        document.title = t('Page_Title');
        CallDB(SearchKey)
    };
    function CallDB(SearchKey) {
        try {
            var raw = JSON.stringify({
                "Case": SearchKey,
                "Language": i18n.language
            });
            console.log(raw);
            fetch('http://localhost:8080/demo/id/', {
                method: 'POST',
                body: raw,
                headers: {
                    'Content-type': 'text/plain; charset=utf-8',
                },
            })
                .then(async response => {
                    const data = await response.json();
                    console.log(data);
                    // check for error response
                    if (!response.ok) {
                        // get error message from body or default to response statusText
                        const error = (data && data.message) || response.statusText;

                        return Promise.reject(error);
                    } else {
                        //only success request will store 
                        setSearchKey(SearchKey);
                        setResponseData(data);
                    }
                })
                .catch(error => console.error("ERROR:" + error));
        }
        catch (error) {
            console.error("Catched ERROR:" + error);
        }
        finally {
            console.log("Done!");
        };
    };

    useEffect(() => {
        document.title = t('Page_Title');
        if (params.state !== null ) {
            console.log(params.state)
            CallDB(params.state)
        }
    }, []);


        return (
            <>

                <div >
                    <Grid.Column>
                        <Sidebar.Pushable>
                            <Sidebar
                                as={Menu}
                                animation='overlay'
                                icon='labeled'

                                onHide={() => setOpen(false)}
                                vertical
                                visible={Open}
                                width='thin'
                            >
                                <Menu.Item as='a' >
                                    <List divided relaxed size='big'>
                                        <List.Item><img src={require('./image/logo.png')} alt="HAD LOGO"></img></List.Item>
                                        <List.Item><img src={require('./image/zhcn.png')} onClick={() => { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") }} alt="zh-cn"></img></List.Item>
                                        <List.Item><img src={require('./image/zhtw.png')} onClick={() => { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") }} alt="zh-tw"></img></List.Item>
                                        <List.Item><img src={require('./image/enus.png')} onClick={() => { changeLanguage("en-US"); setLanguageSelected("en-US") }} alt="English"></img></List.Item>
                                    </List>
                                </Menu.Item>
                            </Sidebar>
                            <Sidebar
                                as={List}
                                animation='overlay'
                                icon='labeled'
                                direction='right'
                                onHide={() => setOpen(false)}
                                vertical
                                visible={Open}
                                width='thin'
                            >
                                <List.Item divided relaxed size='massive' className={Infocss.closebutton} >
                                    X
                                </List.Item>
                            </Sidebar>

                            <Sidebar.Pusher dimmed={Open}>
                                <div className={Infocss.container} >
                                    <div className={Infocss.responsive} ><Icon size='huge' name='bars' className={Infocss.responsiveLogo} onClick={() => setOpen(true)}> </Icon></div>
                                    <div className={Infocss.title}>
                                        <div>
                                            <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") } }} src={require('./image/zhcn.png')} className={LanguageSelected === "zh-CN" ? Infocss.i18nSelected : Infocss.i18n} onClick={() => { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") }} alt="zh-cn"></img>
                                            <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") } }} src={require('./image/zhtw.png')} className={LanguageSelected === "zh-TW" ? Infocss.i18nSelected : Infocss.i18n} onClick={() => { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") }} alt="zh-tw"></img>
                                            <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("en-US"); setLanguageSelected("en-US") } }} src={require('./image/enus.png')} className={LanguageSelected === "en-US" ? Infocss.i18nSelected : Infocss.i18n} onClick={() => { changeLanguage("en-US"); setLanguageSelected("en-US") }} alt="English"></img>
                                            <span tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/text') } }} className={Infocss.textsize} onClick={() => navigate('/text')}> {t('Text')}</span>
                                        </div>
                                    </div>
                                    <div className={Infocss.logo} >
                                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} src={require('./image/logo.png')} className={Infocss.HADLOGO} onClick={() => navigate('/')} alt="HAD LOGO"></img>
                                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} src={require('./image/title_' + i18n.language + '.png')} className={Infocss.HADLOGO} onClick={() => navigate('/')} alt="HAD LOGO"></img>
                                    </div>
                                    <div className={Infocss.menu}>
                                        <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} onClick={() => navigate('/')} aria-label="Home">{t('Home')}</label>
                                        <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/Search', { state: { search: "None" } }) } }} onClick={() => navigate('/Search', { state: { search: "None" } })} aria-label="Search">{t('Search')}</label>
                                        <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/result', { state: { search: "Full" } }) } }} onClick={() => navigate('/result', { state: { search: "Full" } })} aria-label="List All building info">{t('ListAll')}</label>
                                        <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/help') } }} onClick={() => navigate('/help')} aria-label="Help">{t('Help')}</label>
                                    </div>
                                    <div className={Infocss.main}>

                                        {responseData && responseData.length > 0 && responseData.map(item =>
                                            <>
                                                <div className={Infocss.form}>
                                                    <div className={Infocss.Information}>{t('Building_Info')}</div>
                                                    <div className={Infocss.content}>
                                                        <div className={Infocss.box}>
                                                            <div className={Infocss.label}> <label>{t('District')}</label> </div>
                                                            <div className={Infocss.resulttextbox} aria-label="District"> {item.District} </div>
                                                        </div>
                                                        <div className={Infocss.box}>
                                                            <div className={Infocss.label}> <label>{t('Building_Name')}</label> </div>
                                                            <div className={Infocss.resulttextbox} aria-label="Building Name"> {item.Building_Name} </div>
                                                        </div>
                                                        <div className={Infocss.box}>
                                                            <div className={Infocss.label}> <label>{t('Estate')}</label> </div>
                                                            <div className={Infocss.resulttextbox} aria-label="Estate"> {item.Estate} </div>
                                                        </div>
                                                        <div className={Infocss.box}>
                                                            <div className={Infocss.label}> <label>{t('Storeys')}</label> </div>
                                                            <div className={Infocss.resulttextbox} aria-label="Building Storeys"> {item.Storeys} </div>
                                                        </div>
                                                        <div className={Infocss.box}>
                                                            <div className={Infocss.label}> <label>{t('Basement')}</label></div>
                                                            <div className={Infocss.resulttextbox} aria-label="Building Basement"> {item.Basement} </div>
                                                        </div>
                                                        <div className={Infocss.box}>
                                                            <div className={Infocss.label}> <label>{t('Units')}</label> </div>
                                                            <div className={Infocss.resulttextbox} aria-label="Building Unit"> {item.Units} </div>
                                                        </div>
                                                        <div className={Infocss.box}>
                                                            <div className={Infocss.label}> <label>{t('YearBuilt')}</label> </div>
                                                            <div className={Infocss.resulttextbox} aria-label="Built Year info">{item.Year_Built}  </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className={Infocss.form}>
                                                    <div className={Infocss.Information}>{t('Address')}</div>
                                                    <div className={Infocss.content}>
                                                        <div className={Infocss.AddressBox}>
                                                            {item.Address.length > 0 && item.Address.split("|").map(A =>
                                                                <div className={Infocss.AddressTextBox} aria-label="Address"> {A} </div>
                                                            )}
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className={Infocss.form}>
                                                    <div className={Infocss.Information}>{t('Building_Organization')}</div>
                                                    <div className={Infocss.content}>
                                                        <table className={Infocss.InfoTable} aria-label="Organization Table">
                                                            <tr>
                                                                <td className={Infocss.TitleTextBox} >{t('Organization_Type')}</td>
                                                                <td className={Infocss.TitleInfoTextBox} >{t('Building_Organization_Name')}</td>
                                                            </tr>
                                                            {item.OC.length > 0 && item.OC.split("|").map(A =>
                                                                <tr>
                                                                    <td className={Infocss.TextBox} aria-label="OC">{t('OC')}</td>
                                                                    <td className={Infocss.InfoTextBox} aria-label="OC info">{A}</td>
                                                                </tr>
                                                            )}
                                                            {item.OComm.length > 0 && item.OComm.split("|").map(A =>
                                                                <tr>
                                                                    <td className={Infocss.TextBox} aria-label="OComm">{t('OComm')}</td>
                                                                    <td className={Infocss.InfoTextBox} aria-label="OComm info">{A}</td>
                                                                </tr>
                                                            )}
                                                            {item.PMC.length > 0 && item.PMC.split("|").map(A =>
                                                                <tr>
                                                                    <td className={Infocss.TextBox} aria-label="PMC">{t('PMC')}</td>
                                                                    <td className={Infocss.InfoTextBox} aria-label="PMC info">{A}</td>
                                                                </tr>
                                                            )}
                                                            {item.Others.length > 0 && item.Others.split("|").map(A =>
                                                                <tr>
                                                                    <td className={Infocss.TextBox} aria-label="Others">{t('Others')}</td>
                                                                    <td className={Infocss.InfoTextBox} aria-label="Others info">{A}</td>
                                                                </tr>
                                                            )}
                                                        </table>
                                                    </div>
                                                </div>
                                            </>
                                        )
                                        }

                                        <button className={Infocss.backpage} onKeyDown={(event) => { if (event.key === "Enter") { navigate(-1) } }} onClick={() => navigate(-1)} aria-label="Go back">{t('Back')}</button>
                                    </div>
                                    <div className={Infocss.footer}>
                                        <div className={Infocss.footerLeftLabel} tabindex="0" ><img className={Infocss.WCAG} src={require('./image/test.png')} alt='WCAG Logo'></img></div>
                                        <div className={Infocss.footerLeftLabel} tabindex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/notice') } }} onClick={() => navigate('/notice')} aria-label="Notice">{t('Notice')}</div>
                                        <div className={Infocss.footerLeftLabel} tabindex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/policy') } }} onClick={() => navigate('/policy')} aria-label="Policy">{t('Policy')}</div>
                                        <div className={Infocss.footerRightLabel} tabindex="0" aria-label="Copyright">{t('Copyright')}</div>
                                    </div>
                                </div>
                            </Sidebar.Pusher>
                        </Sidebar.Pushable>
                    </Grid.Column>

                </div>

            </>
        )

}
export default Info;