import { yupResolver } from '@hookform/resolvers/yup';
import { useEffect, useState } from 'react';
import Collapsible from 'react-collapsible';
import { useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import { useLocation } from 'react-router';
import { useNavigate } from "react-router-dom";
import * as yup from 'yup';
import Resultcss from './Result.module.css';
import { Pagination, Icon, List, Grid, Menu, Sidebar } from 'semantic-ui-react'




const schema = yup
    .object()
    .shape({
        District: yup.string().nullable(),
        Building_Name: yup.string().nullable(),
        Estate: yup.string().nullable(),
        Street: yup.string().nullable(),
        Village: yup.string().nullable(),
        StreetNo: yup.number().transform((value) => (isNaN(value) ? undefined : value)).nullable(),
        Storeys: yup.number().transform((value) => (isNaN(value) ? undefined : value)).nullable(),
        Units: yup.number().transform((value) => (isNaN(value) ? undefined : value)).nullable(),
        Yearfrom: yup.number().transform((value) => (isNaN(value) ? undefined : value)).nullable(),
        Yearto: yup.number().transform((value) => (isNaN(value) ? undefined : value)).nullable(),
        OC: yup.bool(),
        OComm: yup.bool(),
        PMC: yup.bool(),
        Others: yup.bool(),
        Without: yup.bool()

    })
    .required();

function removeEmptyFields(data) {
    Object.keys(data).forEach(key => {
        if (data[key] === '' || data[key] == null || data[key] === false) {
            delete data[key];
        }
    });
}




const Result = () => {
    const [ResponseData, setResponseData] = useState(null);
    const [SearchKey, setSearchKey] = useState(null);
    const [SearchPage, setSearchPage] = useState(1);
    const [SearchPageSize, setSearchPageSize] = useState(5);
    const [SortKey, setSortKey] = useState("building_id");
    const [SortDirection, setSortDirection] = useState(1);
    const [Open, setOpen] = useState(false);
    const [TotalPage, setTotalPage] = useState(null);
    const [TotalRecord, setTotalRecord] = useState(null);
    const [isTrigger, setTrigger] = useState(false);
    const navigate = useNavigate();
    const params = useLocation();
    const { t, i18n } = useTranslation();
    const [LanguageSelected, setLanguageSelected] = useState(i18n.language);

    const {
        register,
        handleSubmit,
        getValues,
        setValue
    } = useForm({
        resolver: yupResolver(schema)
    });

    const onSubmit = (data, e) => {
        setTrigger(false);
        Object.assign(data, { "search": "Condition" });
        removeEmptyFields(data);
        console.log(data, e);
        CallDB(data, 1, SearchPageSize, SortKey, SortDirection);
    }
    const onError = (errors, e) => console.log(errors, e)

    const changeLanguage = (lng) => {
        i18n.changeLanguage(lng);
        document.title = t('Page_Title');
        CallDB(SearchKey, 1, SearchPageSize, SortKey, SortDirection);
    };

    useEffect(() => {
        document.title = t('Page_Title');
        console.log("Onload");
        console.log(SearchKey);
        console.log(params.state);
        if (SearchKey !== null) {
            CallDB(SearchKey, SearchPage, SearchPageSize, SortKey, SortDirection);
        } else if (params.state !== null) {
            CallDB(params.state, 1, SearchPageSize, SortKey, SortDirection);
        }

    }, []);

    function CallDB(SearchKey, Page, PageSize, Sort, Direction) {
        try {
            //console.log(Page);
            if (Page <= 0) { Page = 1; } //ignore page before first page
            var raw = JSON.stringify({
                "Case": SearchKey,
                "Page": Page,
                "PageSize": PageSize,
                "Sort": Sort,
                "Direction": Direction,
                "Language": i18n.language
            });
            //console.log(raw);
            fetch('http://localhost:8080/demo/sit/', {
                method: 'POST',
                body: raw,
                headers: {
                    'Content-type': 'text/plain; charset=utf-8',
                },
            }).then(async response => {
                const data = await response.json();
                /*//console.log(data);*/
                // check for error response
                if (!response.ok) {
                    // get error message from body or default to response statusText
                    const error = (data && data.message) || response.statusText;

                    return Promise.reject(error);
                } else {
                    //only success request will store 
                    setSearchKey(SearchKey);
                    setSearchPage(Page);
                    setSearchPageSize(PageSize);
                    setSortKey(Sort);
                    setSortDirection(Direction);
                    setTotalPage(data[0].TotalPage);
                    setTotalRecord(data[0].TotalRecord);
                    setResponseData(data[1]);
                    navigate('/result', { state: SearchKey });

                }
            })
                .catch(error => console.error("ERROR:" + error));
        }
        finally {
            console.log("Done!");
        };
    };

    function Sort(key) {
        if (key === SortKey) {
            console.log("Same key");
            CallDB(SearchKey, 1, SearchPageSize, key, -SortDirection );
        } else {
            console.log("Diff key");
            CallDB(SearchKey, 1, SearchPageSize, key, 1);
        }
    }
    function check(e) {
        if (e.target.name === "Without" && !getValues("Without") ) {
            setValue("OC", false);
            setValue("OComm", false);
            setValue("PMC", false);
            setValue("Others", false);
        } else if (e.target.name !== "Without" && getValues("Without")) {
            setValue("Without", false);
        }
    }



    const handlePaginationChange = (e, { activePage }) => CallDB(SearchKey, activePage, SearchPageSize, SortKey, SortDirection) 


    const DistrictVal = ["", "CENTRAL_AND_WESTERN", "WAN_CHAI", "EASTERN", "SOUTHERN", "YAU_TSIM_MONG", "SHAM_SHUI_PO", "KOWLOON_CITY", "WONG_TAI_SIN", "KWUN_TONG", "KWAI_TSING", "TSUEN_WAN", "TUEN_MUN", "YUEN_LONG", "NORTH", "TAI_PO", "SHA_TIN", "SAI_KUNG", "ISLANDS"];

    return (
        <>
            <div >
                <Grid.Column>
                    <Sidebar.Pushable>
                        <Sidebar
                            as={Menu}
                            animation='overlay'
                            icon='labeled'

                            onHide={() => setOpen(false)}
                            vertical
                            visible={Open}
                            width='thin'
                        >
                            <Menu.Item as='a' >
                                <List divided relaxed size='big'>
                                    <List.Item><img src={require('./image/logo.png')} alt="HAD LOGO"></img></List.Item>
                                    <List.Item><img src={require('./image/zhcn.png')} onClick={() => { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") }} alt="zh-cn"></img></List.Item>
                                    <List.Item><img src={require('./image/zhtw.png')} onClick={() => { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") }} alt="zh-tw"></img></List.Item>
                                    <List.Item><img src={require('./image/enus.png')} onClick={() => { changeLanguage("en-US"); setLanguageSelected("en-US") }} alt="English"></img></List.Item>
                                </List>
                            </Menu.Item>
                        </Sidebar>
                        <Sidebar
                            as={List}
                            animation='overlay'
                            icon='labeled'
                            direction='right'
                            onHide={() => setOpen(false)}
                            vertical
                            visible={Open}
                            width='thin'
                        >
                            <List.Item divided relaxed size='massive' className={Resultcss.closebutton} >
                                X
                            </List.Item>
                        </Sidebar>

                        <Sidebar.Pusher dimmed={Open}>
                            <div className={Resultcss.container} >
                                <div className={Resultcss.responsive} ><Icon size='huge' name='bars' className={Resultcss.responsiveLogo} onClick={() => setOpen(true)}> </Icon></div>
                                <div className={Resultcss.title}>
                                    <div>
                                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") } }} src={require('./image/zhcn.png')} className={LanguageSelected === "zh-CN" ? Resultcss.i18nSelected : Resultcss.i18n} onClick={() => { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") }} alt="zh-cn"></img>
                                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") } }} src={require('./image/zhtw.png')} className={LanguageSelected === "zh-TW" ? Resultcss.i18nSelected : Resultcss.i18n} onClick={() => { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") }} alt="zh-tw"></img>
                                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("en-US"); setLanguageSelected("en-US") } }} src={require('./image/enus.png')} className={LanguageSelected === "en-US" ? Resultcss.i18nSelected : Resultcss.i18n} onClick={() => { changeLanguage("en-US"); setLanguageSelected("en-US") }} alt="English"></img>
                                        <span tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/text') } }} className={Resultcss.textsize} onClick={() => navigate('/text')}> {t('Text')}</span>
                                    </div>
                                </div>
                                <div className={Resultcss.logo} >
                                    <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} src={require('./image/logo.png')} className={Resultcss.HADLOGO} onClick={() => navigate('/')} alt="HAD LOGO"></img>
                                    <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} src={require('./image/title_' + i18n.language + '.png')} className={Resultcss.HADLOGO} onClick={() => navigate('/')} alt="HAD LOGO"></img>
                                </div>
                                <div className={Resultcss.menu}>
                                    <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} onClick={() => navigate('/')} aria-label="Home">{t('Home')}</label>
                                    <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/Search', { state: { search: "None" } }) } }} onClick={() => navigate('/Search', { state: { search: "None" } })} aria-label="Search">{t('Search')}</label>
                                    <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/result', { state: { search: "Full" } }) } }} onClick={() => navigate('/result', { state: { search: "Full" } })} aria-label="List All building info">{t('ListAll')}</label>
                                    <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/help') } }} onClick={() => navigate('/help')} aria-label="Help">{t('Help')}</label>
                                </div>
                                <div className={Resultcss.main}>
                                    <div className={Resultcss.collapseItem}>
                                        <div className={Resultcss.collapseTrigger} onClick={() => setTrigger(!isTrigger)} > {!isTrigger ?<Icon name="plus"></Icon>:<Icon name="minus"></Icon>}{t('Search')}</div>
                                        <Collapsible trigger="" open={isTrigger} >
                                            <form onSubmit={handleSubmit(onSubmit, onError)} className={Resultcss.form} >
                                                <div className={Resultcss.box}>
                                                    <div className={Resultcss.label}> <label>{t('District')}</label> </div>
                                                    <div >  <select className={Resultcss.selector} name="section" {...register('District')} aria-label="District Selectot">
                                                        {DistrictVal.map((District) =>
                                                            <option value={District} key={District} aria-label="District selector" selected={params.state && params.state.District === District}>{t(District || 'All_District')}</option>
                                                        )}
                                                    </select> </div>
                                                </div>
                                                <div className={Resultcss.box}>
                                                    <div className={Resultcss.label}> <label aria-label="Building Name">{t('Building_Name')}</label> </div>
                                                    <div> <input aria-label="Building Name input" className={Resultcss.textbox} {...register('Building_Name')} defaultValue={params.state.Building_Name} /> </div>
                                                </div>
                                                <div className={Resultcss.box}>
                                                    <div className={Resultcss.label}> <label aria-label="Estate">{t('Estate')}</label> </div>
                                                    <div> <input className={Resultcss.textbox} aria-label="Estate Input" {...register('Estate')} defaultValue={params.state.Estate} /> </div>
                                                </div>
                                                <div className={Resultcss.box}>
                                                    <div className={Resultcss.label}> <label aria-label="Street">{t('Street')}</label></div>
                                                    <div> <input className={Resultcss.textbox} aria-label="Street Input" {...register('Street')} defaultValue={params.state.Street} /> </div>
                                                </div>
                                                <div className={Resultcss.box}>
                                                    <div className={Resultcss.label}> <label aria-label="Village">{t('Village')}</label> </div>
                                                    <div> <input className={Resultcss.textbox} aria-label="Village Input" {...register('Village')} defaultValue={params.state.Village} /> </div>
                                                </div>
                                                <div className={Resultcss.box}>
                                                    <div className={Resultcss.label}> <label aria-label="StreetNo">{t('StreetNo')}</label> </div>
                                                    <div> <input className={Resultcss.textbox} aria-label="Street No Input" {...register('StreetNo')} defaultValue={params.state.StreetNo} /> </div>
                                                </div>
                                                <div className={Resultcss.box}>
                                                    <div className={Resultcss.label}> <label aria-label="Storeys">{t('Storeys')}</label> </div>
                                                    <div> <input className={Resultcss.textbox} aria-label="Storeys Input" {...register('Storeys')} defaultValue={params.state.Storeys} /> </div>
                                                </div>
                                                <div className={Resultcss.box}>
                                                    <div className={Resultcss.label}> <label aria-label="Units">{t('Units')}</label> </div>
                                                    <div> <input className={Resultcss.textbox} aria-label="Units Input" {...register('Units')} defaultValue={params.state.Units} /> </div>
                                                </div>
                                                <div className={Resultcss.box}>
                                                    <div className={Resultcss.label}> <label aria-label="Year from">{t('Yearfrom')}</label> </div>
                                                    <div> <input className={Resultcss.textbox} aria-label="Year from Input" {...register('Yearfrom')} defaultValue={params.state.Yearfrom} /> </div>
                                                </div>
                                                <div className={Resultcss.box}>
                                                    <div className={Resultcss.label}> <label aria-label="Year to">{t('Yearto')}</label> </div>
                                                    <div> <input className={Resultcss.textbox} aria-label="Year to Input" {...register('Yearto')} defaultValue={params.state.Yearto} /> </div>
                                                </div>

                                                <fieldset className={Resultcss.fieldset}>
                                                    <div>
                                                        <label >{t('Organization_Type')}</label><br></br>
                                                        <label aria-label="OC">{t('OC')}</label>
                                                        <input {...register('OC')} type='checkbox' onChange={check} id='OC' aria-label="OC check box" defaultChecked={params.state.OC} />
                                                    </div>
                                                    <div>
                                                        <label aria-label="OComm">{t('OComm')}</label>
                                                        <input {...register('OComm')} type='checkbox' onChange={check} id='OComm' aria-label="OComm check box" defaultChecked={params.state.OComm} />
                                                    </div>
                                                    <div>
                                                        <label aria-label="PMC">{t('PMC')}</label>
                                                        <input {...register('PMC')} type='checkbox' onChange={check} id='PMC' aria-label="PMC check box" defaultChecked={params.state.PMC} />
                                                    </div>
                                                    <div>
                                                        <label aria-label="Others">{t('Others')}</label>
                                                        <input {...register('Others')} type='checkbox' onChange={check} id='Others' aria-label="Others check box" defaultChecked={params.state.Others} />
                                                    </div>
                                                    <div>
                                                        <label aria-label="Without">{t('Without')}</label>
                                                        <input {...register('Without')} type='checkbox' onChange={check} id='Without' aria-label="Without check box" defaultChecked={params.state.Without} />
                                                    </div>
                                                </fieldset>

                                                <input type="submit" value={t('Submit')} aria-label="Submit" />
                                                <input type="reset" value={t('Reset')} aria-label="Reset" />
                                            </form>
                                        </Collapsible>
                                    </div>
                                    <div className={Resultcss.ResultTable}>
                                        <div className={Resultcss.ResultTableTitle}>{t('Building_Search_Result')}</div>
                                        <table className={Resultcss.table} aria-label="Building info table">
                                                <thead>
                                                    <tr>
                                                    <th className={Resultcss.Name} tabindex="0" onKeyDown={(event) => { if (event.key === "Enter") { Sort("Building_Name") } }} onClick={() => Sort("Building_Name")} aria-label="Building name">{t('Building_Name')}</th>
                                                    <th className={Resultcss.District} tabindex="0" onKeyDown={(event) => { if (event.key === "Enter") { Sort("Address")} }} onClick={() => Sort("Address")} aria-label="Address">{t('Address')}</th>
                                                    <th className={Resultcss.Owner} aria-label="OC">{t('OC')}</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                {ResponseData && ResponseData.length > 0 && ResponseData.map((item,index) =>            
                                                    <tr key={item.building_id}>
                                                        <td tabindex="0" className={(index & 1) ? Resultcss.resultName : Resultcss.resultName2} onClick={() => navigate('/info', { state: { search: "Single", building_id: item.building_id } })} onKeyDown={(event) => { if (event.key === "Enter") { navigate('/info', { state: { search: "Single", building_id: item.building_id } }) } }}>{item.Building_Name}</td>
                                                        <td tabindex="0" className={(index & 1) ? Resultcss.resultDistrict : Resultcss.resultDistrict2} onClick={() => navigate('/info', { state: { search: "Single", building_id: item.building_id } })} onKeyDown={(event) => { if (event.key === "Enter") { navigate('/info', { state: { search: "Single", building_id: item.building_id } }) } }}>{item.Address.split("|")[0]}</td>
                                                        <td className={(index & 1) ? Resultcss.resultOwner : Resultcss.resultOwner2} >{item.OC.split("|").map(A => <p>{A}</p> )}</td>
                                                    </tr>
                                                )
                                                }
                                                </tbody>
                                        </table>
                                        <div className={Resultcss.Pagination}>
                                            <Pagination  defaultActivePage={SearchPage} totalPages={TotalPage} onPageChange={handlePaginationChange} />
                                        </div>

                                        <div className={Resultcss.Pagination}>
                                        {TotalPage === 0 && (
                                            <span>1/1 | </span>
                                        )
                                        }
                                        {TotalPage > 0 && (
                                            <span>{SearchPage}/{TotalPage} | </span>
                                        )
                                        }
                                        {SearchPage === 1 && TotalRecord > 0 && SearchPageSize > TotalRecord && (
                                            <span>
                                                1 - {TotalRecord} of total {TotalRecord} record(s)
                                            </span>
                                        )
                                        }
                                        {SearchPage === 1 && TotalRecord > 0 && SearchPageSize < TotalRecord && (
                                            <span>
                                                1 - {SearchPageSize} of total {TotalRecord} record(s)
                                            </span>
                                        )
                                        }
                                        {SearchPage > 1 && SearchPageSize * SearchPage < TotalRecord &&(
                                            <span>
                                                {SearchPageSize * (SearchPage-1)+1} - {SearchPageSize * SearchPage} of total {TotalRecord} record(s)
                                            </span>    
                                        )
                                        }
                                        {SearchPage > 1 && SearchPageSize * SearchPage >= TotalRecord && TotalRecord >0 &&(
                                            <span>
                                                {SearchPageSize * (SearchPage - 1) + 1} - {TotalRecord} of total {TotalRecord} record(s)
                                            </span>
                                        )
                                        }
                                        {TotalRecord === 0 && (
                                            <span>
                                                0 - 0 of total 0 record(s)
                                            </span>
                                        )
                                        }<select onChange={(event) => CallDB(SearchKey, 1, event.target.value, SortKey, SortDirection)} aria-label="Display count">
                                            <option value="5">5</option>
                                            <option value="10">10</option>
                                            <option value="15">15</option>
                                            <option value="20">20</option>
                                            <option value="25">25</option>
                                        </select>

                                        </div>
                                    </div>
                            </div> 
                                <div className={Resultcss.footer}>
                                    <div className={Resultcss.footerLeftLabel} tabindex="0" ><img className={Resultcss.WCAG} src={require('./image/test.png')} alt='WCAG Logo'></img></div>
                                    <div className={Resultcss.footerLeftLabel} tabindex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/notice') } }} onClick={() => navigate('/notice')} aria-label="Notice">{t('Notice')}</div>
                                    <div className={Resultcss.footerLeftLabel} tabindex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/policy') } }} onClick={() => navigate('/policy')} aria-label="Policy">{t('Policy')}</div>
                                    <div className={Resultcss.footerRightLabel} tabindex="0" aria-label="Copyright">{t('Copyright')}</div>
                                </div>
                        </div>
                        </Sidebar.Pusher>
                    </Sidebar.Pushable>
                </Grid.Column>

            </div>
        </>
    )

}
export default Result;