
import React, {  useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from "react-router-dom";
//import './Page3.css';//each page css
import Textcss from './Text.module.css';
import * as DOMPurify from 'dompurify';
import { List, Icon, Grid, Menu, Sidebar } from 'semantic-ui-react';

const Help = () =>{

    const [Open, setOpen] = useState(false);
    const { t, i18n } = useTranslation();
    const [LanguageSelected, setLanguageSelected] = useState(i18n.language);
    const navigate = useNavigate();

    const sanitized = DOMPurify.sanitize( t('Text_body') );

    const changeLanguage = (lng) => {
        i18n.changeLanguage(lng);
        document.title = t('Page_Title');
    };

    useEffect(() => {
        document.title = t('Page_Title');
    }, []);



    return (
        <>
            <div >
                <Grid.Column>
                    <Sidebar.Pushable>
                        <Sidebar
                            as={Menu}
                            animation='overlay'
                            icon='labeled'

                            onHide={() => setOpen(false)}
                            vertical
                            visible={Open}
                            width='thin'
                        >
                            <Menu.Item as='a' >
                                <List divided relaxed size='big'>
                                    <List.Item><img src={require('./image/logo.png')} alt="HAD LOGO"></img></List.Item>
                                    <List.Item><img src={require('./image/zhcn.png')} onClick={() => { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") }} alt="zh-cn"></img></List.Item>
                                    <List.Item><img src={require('./image/zhtw.png')} onClick={() => { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") }} alt="zh-tw"></img></List.Item>
                                    <List.Item><img src={require('./image/enus.png')} onClick={() => { changeLanguage("en-US"); setLanguageSelected("en-US") }} alt="English"></img></List.Item>
                                </List>
                            </Menu.Item>
                        </Sidebar>
                        <Sidebar
                            as={List}
                            animation='overlay'
                            icon='labeled'
                            direction='right'
                            onHide={() => setOpen(false)}
                            vertical
                            visible={Open}
                            width='thin'
                        >
                            <List.Item divided relaxed size='massive' className={Textcss.closebutton} >
                                X
                            </List.Item>
                        </Sidebar>

                        <Sidebar.Pusher dimmed={Open}>
                            <div className={Textcss.container} >
                                <div className={Textcss.responsive}><Icon size='huge' name='bars' className={Textcss.responsiveLogo} onClick={() => setOpen(true)}> </Icon></div>
                <div className={Textcss.title}>
                    <div>
                                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") } }} src={require('./image/zhcn.png')} className={LanguageSelected === "zh-CN" ? Textcss.i18nSelected : Textcss.i18n} onClick={() => { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") }} alt="zh-cn"></img>
                                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") } }} src={require('./image/zhtw.png')} className={LanguageSelected === "zh-TW" ? Textcss.i18nSelected : Textcss.i18n} onClick={() => { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") }} alt="zh-tw"></img>
                                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("en-US"); setLanguageSelected("en-US") } }} src={require('./image/enus.png')} className={LanguageSelected === "en-US" ? Textcss.i18nSelected : Textcss.i18n} onClick={() => { changeLanguage("en-US"); setLanguageSelected("en-US") }} alt="English"></img>
                                        <span tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/text') } }} className={Textcss.textsize} onClick={() => navigate('/text')}> {t('Text')}</span>
                    </div>
                </div>
                <div className={Textcss.logo} >
                                    <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} src={require('./image/logo.png')} className={Textcss.HADLOGO} onClick={() => navigate('/')} alt="HAD LOGO"></img>
                                    <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} src={require('./image/title_' + i18n.language + '.png')} className={Textcss.HADLOGO} onClick={() => navigate('/')} alt="HAD LOGO"></img>
                </div>
                <div className={Textcss.menu}>
                                    <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} onClick={() => navigate('/')} aria-label="Home">{t('Home')}</label>
                                    <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/Search', { state: { search: "None" } }) } }} onClick={() => navigate('/Search', { state: { search: "None" } })} aria-label="Search">{t('Search')}</label>
                                    <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/result', { state: { search: "Full" } }) } }} onClick={() => navigate('/result', { state: { search: "Full" } })} aria-label="List All building info">{t('ListAll')}</label>
                                    <label tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/help') } }} onClick={() => navigate('/help')} aria-label="Help">{t('Help')}</label>
                </div>
                <div className={Textcss.main}>
                    <div className={Textcss.mainframe}>
                        <div className={Textcss.mainframeTitle} aria-label="Text title">{t('Text_title')}</div>
                        <div dangerouslySetInnerHTML={{ __html: sanitized }} className={Textcss.mainframeContent} aria-label="Text Body" />
                        <br />
                    </div>
                    <button className={ Textcss.backpage} onClick={() => navigate(-1)} aria-label="Go back">{t('Back')}</button>
                </div>
                <div className={Textcss.footer}>
                    <div>
                                        <div className={Textcss.footerLeftLabel} tabindex="0" ><img className={Textcss.WCAG} src={require('./image/test.png')} alt='WCAG Logo'></img></div>
                                        <div className={Textcss.footerLeftLabel} tabindex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/notice') } }} onClick={() => navigate('/notice')} aria-label="Notice">{t('Notice')}</div>
                                        <div className={Textcss.footerLeftLabel} tabindex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/policy') } }} onClick={() => navigate('/policy')} aria-label="Policy">{t('Policy')}</div>
                                        <div className={Textcss.footerRightLabel} tabindex="0" aria-label="Copyright">{t('Copyright')}</div>
                    </div>
                </div>
                            </div>

                        </Sidebar.Pusher>
                    </Sidebar.Pushable>
                </Grid.Column>

            </div>
        </>
    );
}

export default Help;