import React, { useState, useRef, useEffect } from 'react';
import { useNavigate } from "react-router-dom";

import { useTranslation, Trans } from 'react-i18next';
import './i18n';
import Initcss from './init.module.css';
import { List, Icon, Grid, Menu, Sidebar } from 'semantic-ui-react';

const Init = () => {
    const { t, i18n } = useTranslation();
    const [LanguageSelected, setLanguageSelected] = useState(i18n.language);
    const [Open, setOpen] = useState(false);
    const navigate = useNavigate();


    const dialogRef = useRef();

    useEffect(() => {
        document.title = t('Page_Title');
        // Call the showModal function on the dialog element
        dialogRef.current.showModal();
    }, []);

    const changeLanguage = (lng) => {
        i18n.changeLanguage(lng);
        document.title = t('Page_Title');
    };

    const onChange = event => {
        event.preventDefault();
        if (document.getElementById('s1').value === "") {

        } else {
            navigate('/search', { state: { search: "Condition", District: document.getElementById('s1').value } }) ; 
        }

    } 
    return (
        <>
            <div>
                <Grid.Column>
                    <Sidebar.Pushable>
                        <Sidebar
                            as={Menu}
                            animation='overlay'
                            icon='labeled'
                        
                            onHide={() => setOpen(false)}
                            vertical
                            visible={Open}
                            width='thin'
                        >
                            <Menu.Item as='a' >
                                <List divided relaxed size='big'>
                                        <List.Item><img src={require('./image/logo.png')} alt="HAD LOGO"></img></List.Item>
                                        <List.Item><img src={require('./image/zhcn.png')} onClick={() => { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") }} alt="zh-cn"></img></List.Item>
                                        <List.Item><img src={require('./image/zhtw.png')} onClick={() => { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") }} alt="zh-tw"></img></List.Item>
                                        <List.Item><img src={require('./image/enus.png')} onClick={() => { changeLanguage("en-US"); setLanguageSelected("en-US") }} alt="English"></img></List.Item>
                                </List>
                            </Menu.Item>
                        </Sidebar>
                        <Sidebar
                            as={List}
                            animation='overlay'
                            icon='labeled'
                            direction='right'
                            onHide={() => setOpen(false)}
                            vertical
                            visible={Open}
                            width='thin'
                        >
                            <List.Item divided relaxed size='massive' className={Initcss.closebutton} >
                                X
                            </List.Item>
                        </Sidebar>

                    <Sidebar.Pusher dimmed={Open}>
                            <div className={Initcss.container} >
                                <div className={Initcss.responsive}>
                                    <Icon size='huge' name='bars' className={Initcss.responsiveLogo}  onClick={() => setOpen(true)}> </Icon></div>
                                <div className={Initcss.title}>
                                    <div>
                                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") } }} src={require('./image/zhcn.png')} className={LanguageSelected === "zh-CN" ? Initcss.i18nSelected : Initcss.i18n} onClick={() => { changeLanguage("zh-CN"); setLanguageSelected("zh-CN") }} alt="zh-cn"></img>
                                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") } }} src={require('./image/zhtw.png')} className={LanguageSelected === "zh-TW" ? Initcss.i18nSelected : Initcss.i18n} onClick={() => { changeLanguage("zh-TW"); setLanguageSelected("zh-TW") }} alt="zh-tw"></img>
                                        <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { changeLanguage("en-US"); setLanguageSelected("en-US") } }} src={require('./image/enus.png')} className={LanguageSelected === "en-US" ? Initcss.i18nSelected : Initcss.i18n} onClick={() => { changeLanguage("en-US"); setLanguageSelected("en-US") }} alt="English"></img>
                                        <span tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/text') } }} className={Initcss.textsize} onClick={() => navigate('/text')}> {t('Text')}</span>
                                    </div>
                                </div>
                                <div className={Initcss.logo} >
                                    <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} src={require('./image/logo.png')} className={Initcss.HADLOGO} onClick={() => navigate('/')} alt="HAD LOGO"></img>
                                    <img tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/') } }} src={require('./image/title_' + i18n.language + '.png')} className={Initcss.HADLOGO} onClick={() => navigate('/')} alt="HAD LOGO"></img>
                                </div>
                                <div className={Initcss.menu} >
                                </div>
                                <div className={Initcss.main}>
                                    <form id="homeForm" name="homeForm" className={Initcss.homeform}>
                                        <div className={Initcss.selection} >
                                            <label className={Initcss.selectLabel}>{t('District')}  </label>
                                            <select id="s1" tabIndex="0" name="section" onChange={onChange} aria-label="District selector">
                                                <option value="">{t('Select_Section')}</option>
                                                <option value="CENTRAL_AND_WESTERN">{t('CENTRAL_AND_WESTERN')}</option>
                                                <option value="WAN_CHAI">{t('WAN_CHAI')}</option>
                                                <option value="EASTERN">{t('EASTERN')}</option>
                                                <option value="SOUTHERN">{t('SOUTHERN')}</option>
                                                <option value="YAU_TSIM_MONG">{t('YAU_TSIM_MONG')}</option>
                                                <option value="SHAM_SHUI_PO">{t('SHAM_SHUI_PO')}</option>
                                                <option value="KOWLOON_CITY">{t('KOWLOON_CITY')}</option>
                                                <option value="WONG_TAI_SIN">{t('WONG_TAI_SIN')}</option>
                                                <option value="KWUN_TONG">{t('KWUN_TONG')}</option>
                                                <option value="KWAI_TSING">{t('KWAI_TSING')}</option>
                                                <option value="TSUEN_WAN">{t('TSUEN_WAN')}</option>
                                                <option value="TUEN_MUN">{t('TUEN_MUN')}</option>
                                                <option value="YUEN_LONG">{t('YUEN_LONG')}</option>
                                                <option value="NORTH">{t('NORTH')}</option>
                                                <option value="TAI_PO">{t('TAI_PO')}</option>
                                                <option value="SHA_TIN">{t('SHA_TIN')}</option>
                                                <option value="SAI_KUNG">{t('SAI_KUNG')}</option>
                                                <option value="ISLANDS">{t('ISLANDS')}</option>
                                            </select>
                                        </div>
                                        <div className={Initcss.selection} onClick={() => navigate('/search', { state: { search: "None" } })}><label className={Initcss.selectLabel} tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/search', { state: { search: "None" } }) } }} onClick={() => navigate('/search', { state: { search: "None" } })} aria-label="Search page">{t('Search')}</label> { /* Use state to pass parameter in local*/}
                                        </div>
                                        <div className={Initcss.selection} onClick={() => navigate('/search', { state: { search: "Full" } })}><label className={Initcss.selectLabel} tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/search', { state: { search: "Full" } }) } }} onClick={() => navigate('/search', { state: { search: "Full" } })} aria-label="List All building info">{t('ListAll')}</label>
                                        </div>
                                        <div className={Initcss.selection} onClick={() => navigate('/help')}><label className={Initcss.selectLabel} tabIndex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/help')} }} onClick={() => navigate('/help')} aria-label="Help">{t('Help')}</label>
                                        </div>
                                    </form>
                                </div>
                                <div className={Initcss.footer}>
                                    <div className={Initcss.footerLeftLabel} tabindex="0" ><img className={Initcss.WCAG} src={require('./image/test.png')} alt='WCAG Logo'></img></div>
                                    <div className={Initcss.footerLeftLabel} tabindex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/notice') } }} onClick={() => navigate('/notice')} aria-label="Notice">{t('Notice')}</div>
                                    <div className={Initcss.footerLeftLabel} tabindex="0" onKeyDown={(event) => { if (event.key === "Enter") { navigate('/policy') } }} onClick={() => navigate('/policy')} aria-label="Policy">{t('Policy')}</div>
                                    <div className={Initcss.footerRightLabel} tabindex="0" aria-label="Copyright">{t('Copyright')}</div>
                                </div>
                            </div>

                    </Sidebar.Pusher>
                </Sidebar.Pushable>
            </Grid.Column>

            </div>

            <dialog id="dialog" className={Initcss.dialog} ref={dialogRef} aria-label="A dialog window with disclaimer">
                <p className="dialog">{t('Popup_header')}</p>
                <Trans><p className="dialog" aria-label="disclaimer body">{t('Popup_body')}</p></Trans>
                <form method="dialog">
                    <button className={Initcss.dialogButton} aria-label="disclaimer close button">{t('Popup_button')}</button>
                </form>
            </dialog>
        </>
    )
}

export default Init;

